.home {
  width: 100%;
  background-image: url('../../../img/home-bk.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  overflow: auto;
  color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.quote {
  text-align: center;
  margin: 1rem;
}

.container {
  padding-bottom: 5rem;
}

.mainText {
  text-align: center;
  margin: 2rem;
}

.contactlink {
  color: rgb(255, 255, 255);
}

.subText {
  text-align: center;
  margin: 1rem;
}

@media screen and (min-width: 768px) {
  .quote {
    width: 45%;
    text-align: center;
    align-self: center;
  }

  .container {
    width: 60%;
    align-self: flex-end;
    padding-right: 3rem;
  }
}
