.workshop {
  width: 100%;
  background-image: url('../../../img/clouds.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.container {
  flex: 1;
  padding-bottom: 5rem;
}

.paper {
  align-self: center;
  margin: 2rem;
  text-align: center;
  flex: 0;
}

.heading {
  font-weight: 800;
  color: rgb(110, 0, 0);
  padding: 1rem;
  flex: 0;
}

@media screen and (min-width: 1024px) {
  .container {
    margin: 7rem;
  }
}
