.hamburgericon {
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 3rem;
  cursor: pointer;
  z-index: 5;
}

.menu {
  flex: 1;
  padding: 2rem;
  background-color: rgb(66, 66, 66);
}

.menulist {
  display: flex;
  flex-direction: column;
  height: 90%;
  justify-content: space-evenly;
}

.link {
  text-decoration: none;
  color: rgb(255, 255, 255);
  width: 100%;
}

@media screen and (min-width: 768px) {
  .hamburgericon {
    top: 4rem;
    right: 3rem;
    width: 4rem;
  }
}
