.contact {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: auto;
  background-color: rgb(255, 255, 255);
  justify-content: space-between;
  align-items: center;
}

.form {
  width: 80%;
}

.button {
  width: 7rem;
  align-self: center;
  margin: 2rem;
}

@media screen and (min-width: 768px) {
  .form {
    width: 50%;
  }
}
