@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap");

.app {
  display: flex;
  width: 100%;
  height: 100%;
}

.header {
  display: flex;
  width: 90%;
  justify-content: center;
  border-bottom: solid 1px rgb(129, 129, 129);
  align-self: center;
  min-height: 11rem;
}

.logoimg {
  z-index: 5;
  padding: 1rem;
  height: 11rem;
}

.footer {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-bottom: 4rem;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}

.socialicons {
  height: 2rem;
  cursor: pointer;
  margin: 1rem;
}

.footernav {
  color: inherit;
  padding: 0.3rem;
}
